import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";

import CardSkeleton from "./CardSkeleton";
import AboutUs from "./AboutUs";
import ContactUs from "./ContactUs";

import TechSlider from "./TechSlider";

import "swiper/swiper-bundle.min.css";
import "swiper/swiper.min.css";

import "swiper/components/navigation";
import "swiper/components/navigation/navigation.min.css";

import "swiper/components/pagination";
import "swiper/components/pagination/pagination.min.css";

import "swiper/components/autoplay";

// import Swiper core and required modules
import SwiperCore, { Autoplay, Navigation, Pagination } from "swiper";

// install Swiper modules
SwiperCore.use([Pagination]);

SwiperCore.use([Autoplay]);

// install Swiper modules
SwiperCore.use([Navigation]);

const cardList = [
  {
    image: "assets/icon1BW.svg",
    image2: "assets/icon1CLR.svg",
    title: "Enterprise Software Development ",
    title2: "Working ",
    desc: "Create complex enterprise software, ensure reliable software intergration, modernise your legacy system.",
  },
  {
    image: "assets/icon2BW.svg",
    title: "Mobile App Development",
    desc: "We build intuitive and engaging Android, iOS and cross-platform apps for businesses, consumers and enterprises. Which your audience love and adapt to them quickly.",
  },
  {
    image: "assets/icon3BW.svg",
    title: "Website/Web Application",
    desc: "Our web developers create custom websites and web application solutions. We deliver the best Web Presence to help you grow your business.",
  },
  {
    image: "assets/icon4CLR.svg",
    title: "Graphical Design & Video Editing ",
    desc: "Starting from concept, information architecture, visual identity and UI/UX design our team delivers dazzling experiences for maximum user engagement.",
  },
  {
    image: "assets/icon5BW.svg",
    title: "Invoice Billing & Accounting",
    desc: "Complete billing solution for buisness from managing invoices to sending payment reminders to accepting payment online. Also we support all features required to manage your buiness.",
  },
  {
    image: "assets/icon6CLR.svg",
    title: "Digital Marketing & SEO",
    desc: "Let's grow your business together and Converting visitors into customers with potential customers using the internet and other forms of digital platforms",
  },
];

const Main = () => {
  return (
    <div className="mainContainer">
      <div className="section1">
        <div className="verticalSeparator">
          <div className="bigTitle">
            Feel the Innovative Power of Generation. <br /> The Generation of
            us, The Generation of Internet.
          </div>
          <div className="mediumTitle">
            Turn your Amazing Ideas into Reality With <br />
            White Rock Technologies.
          </div>
          <div className="mobileCover">
            <Swiper
              spaceBetween={30}
              centeredSlides={true}
              loop={true}
              speed={1000}
              autoplay={{
                delay: 1000,
                disableOnInteraction: false,
              }}
              allowTouchMove={false}
              className="mySwiper"
            >
              <SwiperSlide>
                <img src="assets/mainart/mainart1.png" alt="" />
              </SwiperSlide>
              <SwiperSlide>
                <img src="assets/mainart/mainart2.png" alt="" />
              </SwiperSlide>
              <SwiperSlide>
                <img src="assets/mainart/mainart3.png" alt="" />
              </SwiperSlide>
              <SwiperSlide>
                <img src="assets/mainart/mainart4.png" alt="" />
              </SwiperSlide>
              <SwiperSlide>
                <img src="assets/mainart/mainart5.png" alt="" />
              </SwiperSlide>
            </Swiper>
          </div>
        </div>
        <div className="artBoard">
          <Swiper
            spaceBetween={30}
            centeredSlides={true}
            loop={true}
            speed={1000}
            direction="horizontal"
            autoplay={{
              delay: 1000,
              disableOnInteraction: false,
            }}
            allowTouchMove={false}
            className="mySwiper"
          >
            <SwiperSlide>
              <img src="assets/mainart/mainart1.png" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src="assets/mainart/mainart2.png" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src="assets/mainart/mainart3.png" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src="assets/mainart/mainart4.png" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src="assets/mainart/mainart5.png" alt="" />
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
      <TechSlider />
      <div className="section2" id="services">
        <div className="maintitle">
          Explore what we <span>offer</span>
        </div>
        <div className="cardsWrapper">
          {cardList.map((item) => (
            <CardSkeleton
              title={item.title}
              desc={item.desc}
              image={item.image}
            />
          ))}
        </div>
      </div>
      <div className="aboutUsSection" id="aboutus">
        <div className="maintitle">
          About<span> Us</span>
        </div>
        <AboutUs />
      </div>
      <div className="contactUsSection" id="contactus">
        <div className="maintitle">
          Let's<span> Talk</span>
        </div>
        <div className="mainInfo">
          <div className="info">
            To request a quote or want to meet up for coffee. Contact us
            directly or fill out the form and we will get back to you promptly
            <br />
            <a href="mailto:Contact@whiterocktechnologies.com">
              Contact@whiterocktechnologies.com
            </a>
          </div>
          <div className="illusMobile">
            <img src="assets/contactUsCoverIllus.png" alt="" />
          </div>
        </div>
        <ContactUs />
      </div>
    </div>
  );
};

export default Main;
